@media (max-width: 1200px ) {
    .social-ul {
        display: none;
    }

    .project-card {
        display: flex;
        flex-direction: column;

        .project-content {
            width: 100%;
        }
    }

    .image {
        img {
            position: relative;
            width: 100%;
        }
    }

    @media (max-width: 550px) {
    .nonessen {
        display: none;
    }

    #contact {
        form {
            input {
                width: 60%;
            }

            #message {
                width: 75%;
                height: 100px;
                resize: both;
            }
        }
    }
    }
}

