#contact {
    display: flex;
    flex-direction: column;
    margin-bottom: 100px;
    
    .data-error {
        color: red;
    }

    .contact-title {
        color: #525f7f;
        font-weight: 500;
        font-size: 1.5em;
        text-align: center;
        padding-top: 50px;
        margin-top: 50px;
        margin-bottom: 50px;
    } 

    form {
        .row {
            margin-bottom: 20px;
            text-align: center;
            .submit {
            width: 35%;
            height: 45px;
            border-radius: 5px;
            border: none;
            background: #747fe0;
            color: #fff;
            font-size: 0.9em;
            font-weight: 600;
        }
        }

        input {
            margin-top: 10px;
            padding: 8px;
            width: 35%;
        }

        textarea {
            margin-top: 10px;
            padding: 8px;
            width: 35%;
        }

        #message {
            width: 55%;
            height: 100px;
            resize: both;
        }
    }
}
