html {
  background-color: #fdfbf7;
  font-family: "Montserrat", sans-serif;
}

.navWrapper {
  display: flex;
  justify-content: flex-end;
  margin: 0 auto;

  .navbar {
    display: flex;
    justify-content: space-around;
    width: 286px;
    font-size: 1.2em;
    margin-top: 30px;
    background-color: transparent;
    box-shadow: none;

    a {
      position: relative;
      text-decoration: none;
      color: #5a5d7a;
      padding: 5px;
      &:hover {
        color: #747fe0;
      }
      &:focus {
        color: #747fe0;
      }
    }
  }
}

.social {
  left: 40px;
  width: 40px;
  position: fixed;
  bottom: 0px;
  right: auto;
  z-index: 10;
  color: rgb(168, 178, 209);
  &:focus {
    color: rgb(168, 178, 209);
  }
}

.social-ul {
  display: flex;
  flex-direction: column;
  -moz-box-align: center;
  align-items: center;
  position: relative;
  padding: 0px;
  margin: 0px auto;
  list-style: outside none none;

  li {
    margin-left: 20px;
  }

  .contact-text {
    color: #5a5d7a;
    letter-spacing: 0.1em;
    writing-mode: vertical-rl;
    a {
      margin: 20px auto;
      padding: 10px;
      display: inline-block;
      text-decoration: none;
      text-decoration-skip-ink: auto;
      color: inherit;
      position: relative;
      transition: all 0.25s cubic-bezier(0.645, 0.045, 0.355, 1) 0s;
      cursor: pointer;

      &:hover {
        transform: translateY(-3px);
        color: #747fe0;
      }

      &:focus {
        color: #747fe0;
      }
    }

    &::after {
      content: "";
      display: inline-block;
      width: 1px;
      height: 90px;
      margin: 0px auto;
      background-color: #5a5d7a;
    }
  }
}

.social-logo {
  display: inline-block;
  max-width: 38px;
  max-height: 46px;
  transition: all 0.25s cubic-bezier(0.645, 0.045, 0.355, 1) 0s;

  &:hover {
    transform: translateY(-3px);
    color: #747fe0;
  }

  svg {
    fill: #5a5d7a;

    &:hover {
      fill: #747fe0;
    }
  }
}



#main {
  display: inline-block;
  margin-top: 50px;
  margin-bottom: 50px;
  width: 100%;
  .hero-details {
    margin: 0px 0px 20px 3px;
    font-size: 16px;
  }
  
  .name {
    font-size: 75px;
    font-weight: 800;
    margin: 0px;
    padding: 0px;
  }

  .detailsContainer {
    display: inline-block;
    max-width: 500px;
  }

}

footer {
  text-align: center;
  padding-bottom: 35px;
  a {
    outline: none;
    color: #000000;
    text-decoration: none;
  }
}
